export const getTableData = (tableData, type, CAData, instanceResponse) => {
  if (!tableData) return [];

  const revisionMap = {};
  
  // Extracting Revision and State from instance response
  if (instanceResponse?.member?.length) {
    instanceResponse.member.forEach((item) => {
      const instance = item?.["dseng:EngInstance"]?.member?.[0];
      const parentId = instance?.parentObject?.identifier;
      const name = instance?.name || "";

      // Extract revision as the part after the last dot (e.g., ".1")
      const revision = name.split(".").pop();
      revisionMap[parentId] = {
        revision,
        state: "N/A", // Add logic here if you have a way to extract 'state'
      };
    });
  }

  return tableData.map((data) => {
    const parentId = data?.ItemId || data?.id;
    const revisionData = revisionMap[parentId] || { revision: "N/A", state: "N/A" };

    if (type === "Change Action") {
      return {
        ItemName: data?.ItemTitle || "N/A",
        Plant: data?.ItemPlants || [],
        ItemId: parentId,
        Revision: revisionData.revision,
        State: revisionData.state,
      };
    } else {
      return {
        "Item Name": data?.title,
        Plant: data?.title || "N/A",
        // Seq: data?.Seq || "1",
        // Status: data?.PlantStatus || "Pending",
        // "MFG Change": data?.MFGChange || "N/A",
        // "MFG Status": data?.MFGStatus || "N/A",
        // Change: data?.Change || CAData.CAName || "N/A",
        // "Change Status": data?.ChangeStatus || CAData.CAStatus || "N/A",
        // "Oracle Template": data.OracleTemplate || "N/A",
        // "ERP Status": "Active" || "N/A",
        // "ERP Export": "Yes" || "N/A",
        // "Lead Plant": false,
        // MBom: data.MBOM ? "Make" : "Buy" || "N/A",
        // "Sort Value": "",
        Revision: revisionData.revision,
        State: revisionData.state,
      };
    }
  });
};


export const getUniqueTableData = (uniqueData) => {
  if (!uniqueData) return [];
  return uniqueData.map((plant) => ({
    "Available Plant": plant?.title || "N/A",
  }));
};

